@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
  font-family: 'Poppins', sans-serif;
}

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1, h2, h3, h4, h5, h6{
  font-family: 'Poppins', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

main {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #FAFAFB;
}

h1, h2, h3, h4, h5, h6 {
    font-weight: 500;
    color: #4F4F4F;
}

small {
    font-size: 10px;
    color: #828282;
}

button {
    font-size: 12px;
    color: #FFFFFF;
    background: #2F80ED;
    padding: 5px 11px;
    border: 1px solid #2F80ED;
    border-radius: 8px;
}

#upload{
    font-size: 12px;
    color: #FFFFFF;
    background: #2F80ED;
    padding: 5px 11px;
    border: 1px solid #2F80ED;
    border-radius: 8px;
}

.upload-wrap{
    width: 402px;
    height: 469px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #FFFFFF; 
    box-shadow: 0px 4px 12px 0px rgba(0,0,0,0.1);
    border-radius: 12px;
}

.add-drop{
    width: 338px;
    height: 219px;
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #F6F8FB;
    border: 1px dashed #97BEF4;
    box-sizing: border-box;
    border-radius: 12px;
}

.after{
    width: 338px;
    height: 219px;
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
}

.highlight{
    background: #c6dcfd;
    border: 1px solid #97BEF4;
    box-sizing: border-box;
}

